import authHelper from "../utils/authHelper";
import config from "../config";

export type WeatherConditions =
  | "Sunny" // no cloud cover during day
  | "Clear" // no cloud cover at night
  | "Mostly Sunny" // <12% clouds
  | "Mostly Clear" // <12% clouds at night
  | "Partly Sunny" // <25% clouds
  | "Partly Clear" // <25% clouds at night
  | "Partly Cloudy" // <50% clouds
  | "Mostly Cloudy" // >50% clouds
  | "Cloudy" // 100% clouds
  | "Chance of Rain"
  | "Rain"
  | "Chance of Thunderstorms"
  | "Thunderstorms"
  | "Chance of Snow"
  | "Snow";

type WeatherIcon =
  | "sunny"
  | "clear"
  | "partlysunny"
  | "partlyclear"
  | "mostlysunny"
  | "mostlyclear"
  | "partlycloudy"
  | "mostlycloudy"
  | "cloudy"
  | "chancerain"
  | "rain"
  | "chancetstorms"
  | "tstorms"
  | "chancesnow"
  | "snow";

export interface Forecast {
  display_city: string; // "SF, CA",
  city: string; // "SF",
  forecast: {
    hourly: {
      time: number; // 1560744000,
      temp: number; // 53.0,
      humidity: number; //93
    }[];
    daily: {
      conditions: WeatherConditions;
      date: number; // 1560754800,
      high_temperature: number; // 67.0,
      icon: WeatherIcon;
      low_temperature: number; // 55.0
    }[];
  };
  now: {
    station_id: string; // "unknown",
    conditions: WeatherConditions; // "Mostly Cloudy",
    current_humidity: number; // 93,
    current_temperature: number; // 11.7,
    current_wind: number; // 12,
    gmt_offset: string; // "-07.00",
    icon: WeatherIcon; // "mostlycloudy",
    sunrise: number; // 1560689220,
    sunset: number; // 1560742380,
    wind_direction: string; //"WSW"
  };
}

class WeatherProvider {
  async get(): Promise<Forecast> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(`${config("serverPath")}/api/weather`, {
      headers: {
        Authorization: token
      }
    });
    const msg = await res.json();
    return msg;
  }
}

const weatherProvider = new WeatherProvider();

export default weatherProvider;
export { WeatherProvider };
