import React from "react";
import Header from "../../components/header/header";
import cameraProvider from "../../providers/cameraProvider";
import style from "./viewCameras.module.css";

interface ViewCamerasProps {}
interface ViewCamerasState {
  images: string[];
  totalCameras: number;
}

class ViewCameras extends React.Component<ViewCamerasProps, ViewCamerasState> {
  state = {
    images: [],
    totalCameras: 0
  };
  mounted: boolean = false;
  async componentDidMount() {
    this.mounted = true;
    const totalCameras = await cameraProvider.getTotalCameras();
    if (!this.mounted) return;
    this.setState({ totalCameras });
    this.fetchImages();
  }
  componentWillUnmount() {
    this.mounted = false;
  }
  fetchImages = async () => {
    if (!this.mounted) return;
    for (let i = 0; i < this.state.totalCameras; i++) {
      try {
        const imageBlob = await cameraProvider.getCameraImage(i);
        const image = URL.createObjectURL(imageBlob);
        if (!this.mounted) break;
        this.setState(state => {
          const images = state.images.slice();
          images.splice(i, 1, image);
          return { images };
        });
      } catch (e) {}
    }

    setTimeout(this.fetchImages, 1000);
  };
  render() {
    const images = this.state.images;
    return (
      <React.Fragment>
        <Header />
        <div
          style={{
            position: "relative",
            height: 415,
            top: 10,
            width: 600,
            margin: "auto"
          }}
        >
          <img
            style={{
              transform: "rotate(43deg)",
              position: "absolute",
              left: 18,
              top: 90,
              zIndex: 1,
              width: 300
            }}
            alt="cam"
            src={images[1]}
          />
          <img
            style={{ position: "absolute", left: 223, width: 300 }}
            alt="cam"
            src={images[2]}
          />
          <img
            style={{
              transform: "rotate(148deg)",
              position: "absolute",
              left: 288,
              top: 119,
              zIndex: 1,
              width: 300
            }}
            alt="cam"
            src={images[0]}
          />
        </div>
        <div style={{ textAlign: "center" }}>
          {this.state.images.map((image, idx) => (
            <img className={style.image} key={idx} alt="cam" src={image} />
          ))}
        </div>
      </React.Fragment>
    );
  }
}

export default ViewCameras;
